@import "variables";
@import "mixins";

body{
  display: grid;
  grid-template-areas: "header"
                       "section-1"
                       "section-2"
                       "section-3"
                       "section-4"
                       "section-5";
  grid-template-columns: 1fr;
  font-size: 16px;
  overflow-x: hidden;
  .header{
    grid-area: header;
    .containerParent();
    align-items: center;
    height: 145px;
    border-bottom: 1px solid #000;
    .container{
      .containerChild();
      display: flex;
      justify-content: space-between;
      .header__name{
        width: 25%;
        text-align: center;
        h2 {
          font: 1.875em/2em @Montser;
        }
        span{
          font: 1em/1.125em @Montser;
        }
      }
      .header__center{
        display: flex;
        flex-direction: column;
        width: 50%;
        .info{
          ul{
            display: flex;
            justify-content: space-between;
            li{
              span{
                font: .875em/1em @OpenS;
                i{
                  color: #933ec5;
                }
              }
              a{
                font: 1.125em/1.25em @Montser;
                color: #000;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
            }
          }
        }
        .border{
          margin: 15px 0 10px;
          border-bottom: 1px solid #959595;
        }
        .nav-panel{
          ul{
            display: flex;
            justify-content: space-around;
            li{
              a{
                font: .875em/1em @RobotoB;
                color: #000;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
      .header__offer {
        width: 25%;
        text-align: center;
        button {
          width: 200px;
          font: .75em/3em @RobotoB;
          color: #533364;
          text-transform: uppercase;
          background: transparent;
          border: 1px solid #933ec5;
          border-radius: 50px;
          box-shadow: 5px 5px 25px #ebebeb;
          outline: none;
          cursor: pointer;
        }
      }
    }
  }
  .section1{
    grid-area: section-1;
    background-color: #ebebeb;
    .containerParent();
    .container{
      .containerChild();
      display: flex;
      justify-content: space-between;
      .appleRepair{
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 500px;
        width: 75%;
        margin-right: 25px;
        padding: 0 25px;
        background-color: #533364;
        border-radius: 50px;
        &__info {
          h4 {
            font: 2.625em/1.5em @Montser;
            color: #fff;
          }
          ul {
            display: flex;
            flex-wrap: wrap;
            li {
              width: 50%;
              margin: 5px 0;
              img {
                vertical-align: middle;
              }
              span {
                font: 1.125em/1.25em @RobotoB;
                color: #fff;
              }
            }
          }
        }
      }
      .sale{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 20%;
        min-height: 500px;
        padding: 0 25px;
        background-color: #eed1a0;
        border-radius: 50px;
        p{
          font: 1.125em/1.25em @RobotoB;
          color: #fff;
          text-align: center;
          span{
            font: 1.875em/2em @Montser;
          }
        }
      }
    }
  }
  .section2{
    grid-area: section-2;
    .containerParent();
    .container {
      .containerChild();
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .aboutUs{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        min-height: 500px;
        border-bottom: 1px solid #ebebeb;
        .info{
          margin-right: 25px;
          h4{
            font: 2.25em/2.375em @Montser;
          }
          p{
            font: 1.25em/1.375em @RobotoB;
          }
          button{
            margin-top: 30px;
            width: 225px;
            height: 55px;
            font: .75em/.875em @RobotoB;
            color: #fff;
            text-transform: uppercase;
            background-color: #533364;
            border-radius: 50px;
            outline: none;
            cursor: pointer;
            box-shadow: 5px 5px 25px #ebebeb;
          }
        }
        .video{
          iframe{
            border-radius: 30px;
            box-shadow: 5px 5px 25px #ebebeb;
          }
        }
      }
      .bottom{
        width: 100%;
        padding: 25px 0;
        ul{
          display: flex;
          justify-content: space-around;
          li{
            p{
              font: 1.125em/1.25em @RobotoM;
            }
          }
        }
      }
    }
  }
  .section3{
    grid-area: section-3;
    min-height: 650px;
    background-color: #ebebeb;
    .containerParent();
    .container{
      .containerChild();
      display: flex;
      justify-content: center;
      align-items: center;
      .whatRepair{
        h4{
          width: 100%;
          font: 2.25em/2.375em @Montser;
          text-align: center;
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          li{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            align-content: space-around;
            width: 170px;
            height: 170px;
            margin: 30px 40px;
            background-color: #fff;
            border-radius: 25px;
            cursor: pointer;
            span{
              margin-top: 10px;
              font: .875em/1em @RobotoB;
              text-align: center;
            }
            &:hover{
              border: 2px solid #533364;
              box-sizing: border-box;
              box-shadow: 0 15px 25px #cecece;
            }
          }
        }
      }
    }
  }
  .section4{
    grid-area: section-4;
    .containerParent();
    .container{
      .containerChild();
      .youCanBuy{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        h4{
          width: 100%;
          font: 2.25em/2.375em @Montser;
          text-align: center;
        }
        .used{
          display: flex;
          align-items: center;
          position: relative;
          width: 45%;
          height: 260px;
          margin: 25px 15px;
          background-color: #fee5eb;
          border-radius: 25px;
          span{
            margin-left: 15px;
            font: 2.25em/1.5em @Montser;
            color: #fff;
          }
          img{
            position: absolute;
            top: 0;
            right: 30px;
          }
        }
        .accesories{
          display: flex;
          align-items: center;
          position: relative;
          width: 45%;
          height: 260px;
          margin: 125px 15px 0 15px;
          background-color: #63c8fa;
          border-radius: 25px;
          span{
            position: relative;
            z-index: 1;
            margin-left: 15px;
            font: 1.875em/1.5em @Montser;
          }
          img{
            position: absolute;
            top: -60px;
            right: -20px;
          }
        }
      }
    }
  }
  .section5{
    grid-area: section-5;
    margin-top: 30px;
    .container{
      .howToFind{
        position: relative;
        .address{
          position: absolute;
          top: 65px;
          left: 55px;
          width: 600px;
          height: 500px;
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: #fff;
          border-radius: 25px;
          h5{
            font: 2.25em/2.5em @Montser;
          }
          ul{
            margin-left: -75px;
            li{
              font: 1em/1.125em @RobotoB;
              p{
                color: #36b3f3;
              }
              span{
                color: #1ca95b;
              }
              a{
                line-height: 1.5em;
                color: #000;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
              &:nth-child(1){
                margin: 20px 0;
              }
              &:nth-child(3){
                margin: 40px 0;
              }
              &:nth-child(5){
                margin: 20px 0;
              }
            }
          }
        }
      }
    }
  }
}